/* COLOR VARIABLE PALETTE */
$primary-white: #fff;
$primary-black: #000;

$shade-greys-1: #F3F5F7;
$shade-greys-2: #EFF0F1;
$shade-greys-3: #DDE0E3;
$shade-greys-4: #CDD1D5;

$tone-light-green: #99F3BD;
$tone-red: #FD5E53;
$tone-orange: #F5BE4F;
$tone-sea-wave: #1597A8;
$tone-light-grey: #E1E8F2;
/* ===== ===== ===== ===== ===== ===== ===== ===== */

/* SHADOW PALETTE */
$shadow-xsmall: 0px 40px 80px rgba(0,0,0, 0.05);
$shadow-small: 0px 4px 60px rgba(0,0,0, 0.07);
$shadow-medium: 0px 0px 40px rgba(0,0,0, 0.1);
$shadow-large: 0px 10px 80px rgba(0,0,0, 0.2);
$shadow-xlarge: 0px 10px 30px rgba(0,0,0, 0.25);
/* ===== ===== ===== ===== ===== ===== ===== ===== */

/* CORNER RADIUS */
$rounded-lg: 20px;
$rounded-md: 10px;
$rounded-sm: 6px;
