		position: relative;
		padding: 5px 0 5px 20px;
		margin: 0;

		&.blockquote--center {
			padding: 5px 0 5px 0;
			text-align: center;

			&:before {
				display: none;
			}
		}
		&.blockquote--right {
			padding: 5px 20px 5px 0;
			text-align: right;

			&:before {
				left: auto;
				right: 0;
			}
		}

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 5px;
			height: 100%;
			background-color: #000;
