	/* ===== ===== ===== ===== ===== */


	/* TABLE */
	&.table {
		width: 100%;
		display: table;
		border: none;
		border-collapse: collapse;
		border-spacing: 0;

		&__overflow {
			overflow: hidden;
			overflow-x: auto;
		}

		&.table--nowrap {
			white-space: nowrap;
		}
		&.table--center {
			thead,
			tbody {
				tr {
					th,
					td {
						text-align: center;
					}
				}
			}
		}
		&.table--striped {
			tr {
				border-bottom: none
			}

			> tbody {
				> tr:nth-child(odd) {
					background-color: rgba(0,0,0,0.05);
				}
			}

			> tbody {
				> tr {
					> td {
						border-radius: 0
					}
				}
			}
		}
		&.table--highlight {
			> tbody {
				> tr {
					transition: background-color .35s ease;
				}
			}

			> tbody {
				> tr:hover {
					background-color: rgba(0,0,0,0.1);
					transition: background-color .35s ease;
				}
			}
		}
		&.table--responsive {
			@include adaptive(1279) {
				position: relative;
				display: block;
				width:100%;
				border-collapse: collapse;
				border-spacing: 0;

				td:empty:before {
					content: '\00a0';
				}

				th,
				td {
					margin: 0;
					vertical-align: top;
				}

				th {
					text-align: left;
				}

				thead {
					display: block;
					float: left;

					tr {
						display: block;
						padding: 0 10px 0 0;

						th::before {
							content: "\00a0";
						}
					}
				}

				tbody {
					display: block;
					width: auto;
					position: relative;
					overflow-x: auto;
					white-space: nowrap;

					tr {
						display: inline-block;
						vertical-align: top
					}
				}

				th {
					display: block;
					text-align: right
				}

				td {
					display: block;
					min-height: 1.25em;
					text-align: left
				}

				tr {
					border-bottom: none;
					padding: 0 10px
				}

				thead {
					border: 0;
					border-right: 1px solid rgba(0,0,0,0.12)
				}
			}
		}

		thead {
			color: #000000;
		}

		tbody {
			color: #2f3542;
		}

		tr {
			border-bottom: 1px solid rgba(0,0,0,0.12)
		}

		td,
		th {
			border: none;
			padding: 15px 10px;
			display: table-cell;
			text-align: left;
			vertical-align: middle;
			border-radius: 2px
		}
	}
	/* ===== ===== ===== ===== ===== */


	/* ACCORDION */
	&.accordion {
		display: flex;
		flex-direction: column;
		border-radius: 5px;
		border: 1px solid rgba(#000, 0.1);
		background-color: transparent;

		.accordion {
			&__box {
				display: flex;
				flex-direction: column;
				width: 100%;

				&:not(:last-of-type) {
					.accordion {
						&__box {
							&-head {
								border-bottom: 1px solid rgba(#000, 0.1);
							}
							&-body {
								> div {
									border-bottom: 1px solid rgba(#000, 0.1);
								}
							}
						}
					}
				}
				&:last-of-type {
					.accordion {
						&__box {
							&-body {
								> div {
									border-top: 1px solid rgba(#000, 0.1);
								}
							}
						}
					}
				}

				&-head {
					cursor: pointer;
					display: flex;
					align-items: center;
					justify-content: space-between;
					width: 100%;
					min-height: 55px;
					padding: 5px 10px;
					background-color: transparent;

					> div {
						display: flex;
					}

					&.is-active {
						background-color: rgba(#000, 0.025);

						svg {
							transform: scaleY(-1);
						}
					}
				}

				&-body {
					display: none;
					width: 100%;

					> div {
						padding: 10px;
					}
				}
			}
		}
	}
