	!* DROPDOWN *!
	&.dropdown {
		position: relative;

		&.is-open {
			.st.dropdown {
				&__toggle {
					border-color: #21BF73;
					@include transition(border-color);

					> div {
						i {
							&:before,
							&:after {
								background-color: #21bf73;
								@include transition(background-color);
							}
						}
					}
				}

				&__menu {
					opacity: 1;
					visibility: visible;
					transform: translateY(0);
					@include transition(opacity, visibility, transform);
				}
			}
		}

		&__toggle {
			position: relative;
			display: flex;
			align-items: center;
			width: 100%;
			height: 48px;
			border-radius: 6px;
			border: 1px solid #CBDBE3;
			background-color: #FFFFFF;
			@include transition(border-color);

			> div {
				&:nth-of-type(1) {
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
					flex: 1;
					min-width: 1px;
					max-width: 100%;
					padding-left: 16px;
					padding-right: 16px;
				}
				&:nth-of-type(2) {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 40px;
					height: 100%;
				}

				p {
					font-weight: 600;
					font-size: 14px;
					color: #000000;
				}

				i {
					position: relative;
					display: flex;
					align-items: center;
					width: 14px;
					height: 14px;

					&:before,
					&:after {
						content: '';
						position: absolute;
						top: 50%;
						width: 65%;
						height: 2px;
						border-radius: 3px;
						background-color: #000000;
						@include transition(background-color);
					}

					&:before {
						left: -2px;
						transform: translate(1.2px, -50%) rotate(43deg);
					}

					&:after {
						right: -2px;
						transform: translate(-1.2px, -50%) rotate(-43deg);
					}
				}
			}
		}

		&__menu {
			z-index: 99;
			opacity: 0;
			visibility: hidden;
			transform: translateY(3px);
			overflow: hidden;
			overflow-y: auto;
			position: absolute;
			top: 100%;
			left: 0;
			display: flex;
			flex-direction: column;
			width: 100%;
			max-height: 216px;
			padding: 8px 0;
			box-shadow: 0 0 5px rgba(0, 0, 0, 0.05), 0 25px 35px rgba(0, 0, 0, 0.03);
			border-radius: 4px;
			background-color: #FFFFFF;
			@include transition(opacity, visibility, transform);
		}

