			font-size: 14px;
			color: #163A5F;
			@include transition(background-color, color);

			&:hover {
				color: #21BF73;
				@include transition(background-color, color);
			}

			&.is-active {
				background-color: rgba(#21BF73, 0.05);
				@include transition(background-color, color);

				&:before,
				&:after {
					opacity: 1;
					visibility: visible;
					@include transition(opacity, visibility);
				}
			}

			&:before,
			&:after {
				opacity: 0;
				visibility: hidden;
				content: '';
				position: absolute;
				right: 0;
				height: 2px;
				border-radius: 3px;
				background-color: #21bf73;
				@include transition(opacity, visibility);
			}

			&:before {
				right: 22px;
				width: 6px;
				transform: translate(-0.25px, 1.25px) rotate(45deg);
			}

			&:after {
				right: 16px;
				width: 10px;
				transform: rotate(-45deg);
			}
		}
	}
